			this.is_online = navigator.onLine
			if(this.is_online){
				this.getGithubData()
			} else {
				this.repos = git_data
				prj_data.forEach((e) => {
					if(e.github_name){
						e.github = this.getGithubByName(e.github_name)
					}
				})
				this.project = prj_data
			}
