				this.desc_animated = '<span class="text-info">&lt;</span><span class="text-danger">p</span> <span class="text-warning"><i>class</i></span><span class="text-info">="</span><span class="text-success">text-center</span><span class="text-info">"</span><span class="text-info">&gt;</span>There are <span class="text-info">{{</span>project.length<span class="text-warning">|</span><span class="text-info">}}</span> project that I have been done<span class="text-info">&lt;/</span><span class="text-danger">p</span><span class="text-info">&gt;</span>'
			}, 26900)
			setTimeout(() => {
				this.desc_animated = 'There are '+this.project.length+' project that I have been done'
			}, 30000)
		}
	},
	mounted(){
		this.updatePageview() // call mixins
		this.initializeProject()
		this.descAnimated()
		// scroll func
		this.handleScroll()
		window.addEventListener('scroll', this.handleScroll)
		// hide element on mounted
		var elements = document.getElementsByClassName('jo-show')
		for(var i = 0; i < elements.length; i++){
			elements[i].classList.add('hide-show')
		}


		window.scroll({
			top: 0,
			behavior: 'smooth'
		})
	}
}
</script>